<template>
  <div class="sect">
    <h1 v-if="!currentSection.error">{{currentSection.title}}</h1>
  </div>
</template>
<script>
  // @ is an alias to /src
  import HelloWorld from '@/components/HelloWorld.vue'

  export default {
    name: 'sect',
    computed:{
      currentSection: function () {
        let sect = {error: true};

        if (this.$route.params.sectionCode) {
          let curSect = this.$store.state.sections[this.$route.params.sectionCode];
          if (curSect) {
            sect.error = false;
            sect.title = curSect.title;
          }
        }
        return sect;
      }


    },
    components: {

    }
  }
</script>